.projects-main {
  padding: 2rem 1rem;
  min-height: 100vh;
  overflow-x: hidden;
  box-sizing: border-box;
}

.projects-container {
  max-width: 95vw;
  margin: 0 auto;
  width: 100%;
  padding: 0 1rem;
  margin-top: 1rem;
}

.projects-header {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-bottom: 2rem;
  margin-top: 1rem;
  gap: 1.5rem;
}

.projects-header h1 {
  font-size: clamp(2rem, 5vw, 3.5rem);
  color: var(--primary-color);
  font-family: 'Share Tech Mono', monospace;
  text-shadow: 0 0 8px rgba(255, 213, 44, 0.5);
  margin: 0;
  line-height: 1.2;
}

.archive-link {
  color: var(--primary-color);
  font-family: 'VT323', monospace;
  font-size: clamp(1rem, 2.5vw, 1.3rem);
  text-decoration: none;
  border: 1px solid var(--primary-color);
  padding: 0.8rem 1.5rem;
  border-radius: 4px;
  transition: all 0.3s ease;
  background: transparent;
  white-space: nowrap;
}

.archive-link:hover {
  background: rgba(255, 213, 44, 0.1);
  box-shadow: 0 0 15px rgba(255, 213, 44, 0.3);
  transform: translateY(-2px);
}

.no-projects {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 60vh;
}

.terminal-loading {
  border: 2px solid var(--primary-color);
  padding: 3rem;
  background: rgba(0, 0, 0, 0.9);
  border-radius: 8px;
  box-shadow: 
    0 0 20px rgba(255, 213, 44, 0.3),
    inset 0 0 20px rgba(255, 213, 44, 0.1);
  max-width: 90vw;
}

.loading-text {
  color: var(--primary-color);
  font-family: 'VT323', monospace;
  font-size: clamp(1rem, 2.5vw, 1.4rem);
  line-height: 1.6;
  text-align: center;
  text-shadow: 0 0 4px var(--primary-color);
  animation: textGlow 2s ease-in-out infinite alternate;
}

@keyframes textGlow {
  from { text-shadow: 0 0 4px var(--primary-color); }
  to { text-shadow: 0 0 8px var(--primary-color), 0 0 12px var(--primary-color); }
}

.featured-projects {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.project-card {
  position: relative;
  padding: 1rem 0;
  border-bottom: 1px solid rgba(255, 213, 44, 0.2);
  width: 100%;
}

.project-card:last-child {
  border-bottom: none;
}

.project-content {
  width: 100%;
  margin: 0;
  padding: 2rem;
  border: 2px solid var(--primary-color);
  background: rgba(0, 0, 0, 0.4);
  box-shadow: 
    0 0 20px rgba(255, 213, 44, 0.1),
    inset 0 0 20px rgba(255, 213, 44, 0.05);
}

.project-featured {
  color: var(--primary-color);
  font-family: 'VT323', monospace;
  font-size: 1rem;
  margin-bottom: 1rem;
  text-transform: uppercase;
  letter-spacing: 2px;
  opacity: 0.9;
  border: 2px solid rgba(255, 213, 44, 0.3);
  padding: 0.5rem 1rem;
  width: fit-content;
}

.project-title {
  font-size: clamp(1.5rem, 3.5vw, 2.5rem);
  margin-bottom: 1.2rem; 
}

.project-title a {
  color: var(--primary-color);
  font-family: 'Share Tech Mono', monospace;
  text-decoration: none;
  transition: all 0.3s ease;
  text-shadow: 0 0 4px rgba(255, 213, 44, 0.3);
}

.project-title a:hover {
  color: var(--secondary-color);
  text-shadow: 0 0 12px rgba(255, 213, 44, 0.6);
}

/* Compact description style */
.project-description {
  background: rgba(0, 0, 0, 0.6);
  border: 1px solid rgba(255, 213, 44, 0.3);
  border-radius: 4px;
  padding: 1.5rem; 
  margin-bottom: 1.5rem; 
  box-shadow: inset 0 0 10px rgba(0, 0, 0, 0.3);
  position: relative;
  width: 100%;
  box-sizing: border-box;
}

.project-description::before {
  content: '';
  position: absolute;
  top: -1px;
  left: -1px;
  right: -1px;
  bottom: -1px;
  z-index: -1;
  border-radius: 8px;
  opacity: 0.2;
}

.project-description p {
  color: var(--primary-color);
  font-family: 'VT323', monospace;
  font-size: clamp(1rem, 2vw, 1.2rem);
  line-height: 1.5;
  margin: 0;
  max-width: none;
}

.project-tech-section {
  margin-bottom: 1.5rem;
}

.tech-label {
  color: var(--primary-color);
  font-family: 'Share Tech Mono', monospace;
  font-size: 1.1rem;
  margin-bottom: 1rem;
  text-shadow: 0 0 4px rgba(255, 213, 44, 0.3);
  font-weight: normal;
  border-bottom: 1px solid rgba(255, 213, 44, 0.3);
  padding-bottom: 0.5rem;
}

.tech-name {
  color: var(--primary-color);
  font-weight: bold;
}

.tech-purpose {
  color: var(--primary-color);
  opacity: 0.8;
  font-style: italic;
}

.project-tech-list {
  display: flex;
  flex-direction: column;
  gap: 0.4rem;
  list-style: none;
  margin-bottom: 1.2rem;
  padding: 0;
  width: 100%;
}

.project-tech-list li {
  color: var(--primary-color);
  font-family: 'VT323', monospace;
  font-size: 1rem;
  position: relative;
  padding-left: 1.5rem;
  transition: all 0.3s ease;
}

.project-tech-list li::before {
  content: '▸';
  position: absolute;
  left: 0;
  color: var(--primary-color);
  text-shadow: 0 0 4px rgba(255, 213, 44, 0.5);
  transition: all 0.3s ease;
}

.project-tech-list li:hover {
  text-shadow: 0 0 6px rgba(255, 213, 44, 0.4);
  transform: translateX(3px);
}

.project-tech-list li:hover::before {
  text-shadow: 0 0 8px rgba(255, 213, 44, 0.8);
  transform: scale(1.2);
}.project-links {
  display: flex;
  gap: 1.5rem; 
  margin-top: 1.5rem;
  padding-top: 1rem;
  border-top: 1px solid rgba(255, 213, 44, 0.3);
}

.project-links a {
  color: var(--primary-color);
  font-size: 1.5rem; 
  transition: all 0.3s ease;
  text-shadow: 0 0 4px rgba(255, 213, 44, 0.3);
  padding: 0.5rem;
  border: 1px solid rgba(255, 213, 44, 0.3);
  border-radius: 4px;
  background: rgba(255, 213, 44, 0.05);
}

.project-links a:hover {
  color: var(--secondary-color);
  text-shadow: 0 0 12px rgba(255, 213, 44, 0.8);
  transform: translateY(-3px) scale(1.1);
}

.project-stats {
  color: var(--primary-color);
  font-family: 'VT323', monospace;
  font-size: 1rem;
  margin-bottom: 0.3rem;
  display: flex;
  align-items: center;
  gap: 0.6rem;
}

.project-stats i {
  color: var(--primary-color);
}

.project-card-link {
  text-decoration: none;
  color: inherit;
  display: block;
  transition: all 0.3s ease;
}

.project-card-link:hover .project-card {
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(255, 213, 44, 0.2);
}

@media (max-width: 768px) {
  .projects-main {
    padding: 3rem 1rem 1rem 1rem;
  }
  
  .projects-container {
    padding: 0 0.5rem;
    margin-top: 1rem;
  }
  
  .projects-header {
    flex-direction: column;
    gap: 2rem;
    text-align: center;
    margin-bottom: 3rem;
    margin-top: 1rem;
  }
  
  .featured-projects {
    gap: 3rem;
  }
  
  .project-card {
    padding: 1.5rem 0;
  }
  
  .project-description {
    padding: 1.5rem;
  }
  
  .terminal-loading {
    padding: 2rem;
    margin: 0 0.5rem;
  }
  
  .project-tech-list {
    gap: 0.3rem;
  }
  
  .project-tech-list li {
    font-size: 0.9rem;
  }
  
  .project-links a {
    font-size: 1.8rem;
  }
  
  .project-stats {
    font-size: 1.1rem;
  }
}

@media (max-width: 480px) {
  .projects-main {
    padding: 2rem 0.5rem 0.5rem 0.5rem;
  }
  
  .projects-container {
    margin-top: 0.5rem;
  }
  
  .projects-header {
    margin-bottom: 2rem;
    margin-top: 0.5rem;
  }
  
  .featured-projects {
    gap: 2.5rem;
  }
  
  .project-card {
    padding: 1rem 0;
  }
  
  .project-description {
    padding: 1.25rem;
  }
  
  .project-tech-list {
    gap: 0.25rem;
  }
  
  .project-tech-list li {
    font-size: 0.85rem;
  }
  
  .project-links {
    gap: 1.5rem;
  }
  
  .project-links a {
    font-size: 1.6rem;
  }
  
  .terminal-loading {
    padding: 1.5rem;
  }
  
  .archive-link {
    padding: 0.6rem 1.2rem;
  }
}

@media (min-width: 1200px) {
  .projects-container {
    max-width: 90vw;
  }
  
  .featured-projects {
    gap: 1.8rem;
  }
  
  .project-card {
    padding: 1.2rem 0;
  }
  
  .project-description {
    padding: 1.8rem;
  }
}

@media (min-width: 1400px) {
  .projects-container {
    max-width: 85vw; 
  }
  
  .featured-projects {
    gap: 2rem;
  }
  
  .project-card {
    padding: 1.5rem 0;
  }
  
  .project-description {
    padding: 2rem;
  }
}